import CallBack from "../UI/CallBack"
import AnchorLink from 'react-anchor-link-smooth-scroll'

export default function Header() {
  return (
    <header className="header">
        <div className="header__cont">
            <a className="header__logo" href="/">
                <img className="header__logo-icon" src="img/Header/logo.svg" alt="LEDSPECTRUM" />
            </a>
            <nav className="header__nav">
                <AnchorLink className="header__nav-link" href="#products"> </AnchorLink>
                <AnchorLink className="header__nav-link" href="#tech">. </AnchorLink>
                <AnchorLink className="header__nav-link" href="#content"></AnchorLink>
                <AnchorLink className="header__nav-link" href="#install"></AnchorLink>
                <AnchorLink className="header__nav-link" href="#cash">  </AnchorLink>
            </nav>
            <CallBack/>
        </div>
    </header>
  )
}
